<template>
    <div id="bank" style="padding:5% 20%;width:50%;text-align:center">
      <el-form :model="form" label-width="100px" style="padding:4% 20% 5% 15%;background-color:#fff;border-radius:8px;">
        <div style="font-size:17px;margin:7% 0px;">
            <b>转账操作</b>
        </div>
        <el-form-item label="转账银行：">
            <el-input v-model="form.fromBankId" placeholder="请输入转账银行名称"></el-input>
        </el-form-item>
        <el-form-item label="转账用户：">
            <el-input v-model="form.fromAccountId" placeholder="请输入转账用户名称"></el-input>
        </el-form-item>
        <el-form-item label="收款银行：">
            <el-input v-model="form.toBankId" placeholder="请输入收款银行名称"></el-input>
        </el-form-item>
        <el-form-item label="收款用户：">
            <el-input v-model="form.toAccountId" placeholder="请输入收款用户名称"></el-input>
        </el-form-item>
         <el-form-item label="转账金额：">
            <el-input v-model="form.amount" placeholder="请输入转账金额"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-button type="primary" @click="transfer">转账</el-button>
            <el-button type="success" @click="storage">保存记录</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
export default {
  name: 'Transform',
  data () {
    return {
      form: {},
      balance: null
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var localStr = localStorage.getItem('transform')
      if (localStr === null) {
        return
      }
      this.form = JSON.parse(localStorage.getItem('transform'))
    },
    storage () {
      localStorage.setItem('transform', JSON.stringify(this.form))
    },
    transfer () {
      console.log(this.form)
      this.form.amount = parseInt(this.form.amount)
      var that = this
      this.$axios.post('/v1/transfer', JSON.stringify(this.form))
        .then(function (response) {
          if (response.data.validCode === 0) {
            that.$message.success('转账成功')
          } else {
            that.$message.error(response.data.msg)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>



